import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class RebatesCampaignClose extends Component {
    constructor() {
        super();
    }

    render() {
        const btn_style = {
            "width": "20px",
            "height": "20px",
            "cursor": "pointer",
        };

        return (
            <img src={platform_api_root.runtime.getURL("/img/rebates_close_button.png")}
                 onClick={() => this.props.onClose()}
                 style={btn_style}/>
        );
    }
}

RebatesCampaignClose.propTypes = {
    onClose: PropTypes.func,
};
