import React, { Component } from 'react';

export default class RebatesPopupActivate extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const button_style = {
            width: "300px",
            boxSizing: "border-box",
            backgroundColor: "#069907",
            border: "none",
            cursor: "pointer",
            color: "#ffffff",
            fontWeight: "bold",
            fontSize: "12px",
            lineHeight: "100%",
            height: "30px",
            borderRadius: "3px",
            textIndent: "0",
            letterSpacing: "0.6px",
            fontFamily: "'ノーサンズ','Noto Sans', 'Noto Sans CJK JP', 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'メイリオ', 'Meiryo', 'Osaka', '游ゴシック', 'Yu Gothic', sans-serif",
        };

        return (
            <button style={button_style}>ポイントバック適用中!</button>
        );
    }
}
