import React, { Component } from 'react';
import PropTypes from 'prop-types';

import {
    REBATES_DEFAULT_STORE_EXCLUSION,
    REBATES_DOMAIN_FULL_ADDRESS
} from '../../../../../utilities/src/rebates_const';

export default class RebatesWhatIs extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const link_style = {
            "textAlign": "left",
            "fontSize": "13px",
            "verticalAlign": "middle",
            "margin": "0",
            "padding": "2px 5px 0 0",
            "lineHeight": "100%",
            "letterSpacing": "0.6px",
        };

        const message_style = {
            "display": "inline-block",
            "float": "left",
            "marginTop": "5px",
        };

        const message_highlight_style ={
            "textDecoration": "underline",
            "color": "#1A0DAB",
            "fontWeight": "bold",
        };

        const url_exclusion_path =
            (this.props.rebates_info.urlName)
                ? this.props.rebates_info.urlName + "/#terms"
                : REBATES_DEFAULT_STORE_EXCLUSION;

        return (
            <p style={link_style}>
                <span style={message_style}>※一部
                    <a target="_blank" rel="nofollow noopener noreferrer"
                       href={`${REBATES_DOMAIN_FULL_ADDRESS}/${url_exclusion_path}`}
                       style={message_highlight_style}>ポイント対象外条件</a>があります
                </span>
            </p>
        );
    }
}

RebatesWhatIs.propTypes = {
    rebates_info: PropTypes.object,
};
