import React, { Component } from 'react';
import PropTypes from 'prop-types';

import RebatesPopupActivate from './RebatesPopupActivate';
import RebatesPopupNonActivate from './RebatesPopupNonActivate';
import RebatesBalloon from './RebatesBalloon';
import RebatesCloseButton from './RebatesCloseButton';
import RebatesWhatIs from './RebatesWhatIs';
import RebatesAboutIcon from './RebatesAboutIcon';

import {REBATES_IMAGES_DOMAIN, REBATES_LOGO_SVG} from "../../../../../utilities/src/rebates_const";

export default class RebatesPopup extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const div_style = {
            "width": "340px",
            "height": (this.props.activated) ? "100px" : "120px",
            "textAlign": "left",
            "background": "white",
            "backgroundSize": "100% 100%",
            "backgroundRepeat": "no-repeat",
            "boxSizing": "border-box",
            "fontFamily": "'ノーサンズ','Noto Sans', 'Noto Sans CJK JP', 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'メイリオ', 'Meiryo', 'Osaka', '游ゴシック', 'Yu Gothic', sans-serif",
            "paddingLeft": "20px",
        };

        const backgroundImagePropName =
            (this.props.activated) ? "activated_image" : "inactivated_image";
        const backgroundImagePath = this.props.backgrounds[backgroundImagePropName];
        if (backgroundImagePath) {
            div_style.backgroundImage =
                "url(" + REBATES_IMAGES_DOMAIN + "/" + backgroundImagePath + ")";
        }

        const img_style = {
            "verticalAlign": "bottom",
            "marginTop": "10px",
        };

        const icon_box_style = {
            float: "right",
            padding: "5px 10px",
            display: "inline-block",
        };

        return (
            <div style={div_style}>
                <img style={img_style} src={REBATES_LOGO_SVG} width={100} height={20}/>
                <div style={icon_box_style}>
                    <RebatesAboutIcon/>
                    <RebatesCloseButton onClose={this.props.onClose}/>
                </div>
                {
                    this.props.activated
                        ? <RebatesPopupActivate/>
                        : <>
                              <RebatesBalloon/>
                              <RebatesPopupNonActivate
                                   rebates_info={this.props.rebates_info}
                                   onActivate={this.props.onActivate}/>
                          </>
                }
                <RebatesWhatIs rebates_info={this.props.rebates_info}/>
            </div>
        );
    }
}

RebatesPopup.propTypes = {
    backgrounds: PropTypes.object,
    rebates_info: PropTypes.object,
    activated: PropTypes.bool,
    onActivate: PropTypes.func,
    onClose: PropTypes.func,
};
