import React, {Component} from 'react';
import PropTypes from 'prop-types';

export default class RebatesCampaignText extends Component{
    constructor(props) {
        super(props)
    }

    componentDidMount() {
        this.link.addEventListener("click", (e) => {
            e.preventDefault();
            this.props.onForward();
        });
    }

    render() {
        const text_wrapper = {
            "width": "292px",
            "height": "auto",
            "textAlign": "left",
        };

        const a_style = {
            "textDecoration": "none",
            "color": "#333333",
        };

        const title = {
            "color": "#BF0000",
            "fontSize": "15px",
            "letterSpacing": "0.8px",
        };

        const summary_text ={
            "fontSize": "12px",
            "letterSpacing": "0.6px",
        };

        return (
            <div style={text_wrapper}>
                <a style={a_style} href={this.props.linkUrl} ref={c => this.link = c}>
                    <strong style={title}>{this.props.campaignInfo}</strong>
                    <br/>
                    <span style={summary_text}>{this.props.campaignSummary}</span>
                </a>
             </div>
        );
    }
}

RebatesCampaignText.propTypes = {
    linkUrl: PropTypes.string,
    campaignInfo: PropTypes.string,
    campaignSummary: PropTypes.string,
    onForward: PropTypes.func,
};
