import React from 'react';
import {render} from 'react-dom';

import RebatesPopup from './components/rebates/RebatesPopup';
import RebatesCampaign from './components/rebates/campaign/RebatesCampaign';

import {validateRuntimeOnUserInteraction} from '../../../utilities/src/runtime_helper';

const activatePopupHandler = () => {
  validateRuntimeOnUserInteraction(() => {
    window.parent.postMessage({"method": "RTBcs.RebatesManager.activatePopup"}, '*');
  });
};

const closePopupHandler = () => {
  validateRuntimeOnUserInteraction(() => {
    window.parent.postMessage({"method": "RTBcs.RebatesManager.closePopup"}, '*');
  });
};

const forwardCampaignHandler = () => {
  validateRuntimeOnUserInteraction(() => {
    window.parent.postMessage({"method": "RTBcs.RebatesManager.forwardCampaign"}, '*');
  });
};

const closeCampaignHandler = () => {
  validateRuntimeOnUserInteraction(() => {
    window.parent.postMessage({"method": "RTBcs.RebatesManager.closeCampaign"}, '*');
  });
};

window.addEventListener('message', function(e) {
  const msg = e.data;
  if (msg.method == 'renderPopup') {
    render(<RebatesPopup backgrounds={msg.hover_images}
                         rebates_info={msg.rebates_info}
                         activated={msg.is_activated}
                         onActivate={activatePopupHandler}
                         onClose={closePopupHandler}/>,
           document.getElementById('content'));
  } else if (msg.method == 'renderCampaign') {
    render(<RebatesCampaign toolbarCampaign={msg.campaign}
                            onForward={forwardCampaignHandler}
                            onClose={closeCampaignHandler}/>,
           document.getElementById('content'));
  }
});
