import React, { Component } from 'react';

export default class RebatesBalloon extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const text_style = {
            "boxSizing": "border-box",
            "fontSize" : "12px",
            "letterSpacing": "0.6px",
            "display": "inline-block",
            "padding": "4px 20px 4px 0px",
        };

        return (
            <span style={text_style}>ここからRebates経由のお買物で</span>
        );
    }
}
