import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class RebatesCloseButton extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.btn.addEventListener("click", () => {
            this.props.onClose();
        });
    }

    render() {
        const btn_style = {
            "width": "20px",
            "padding": "3px 0px 0px",
            "cursor": "pointer",
        };

        return (
            <img src={platform_api_root.runtime.getURL("/img/rebates_close_button.png")}
                 style={btn_style} ref={c => this.btn = c}/>
        );
    }
}

RebatesCloseButton.propTypes = {
    onClose: PropTypes.func,
};
