import React, {Component} from 'react';
import PropTypes from 'prop-types';

import RebatesCampaignClose from './RebatesCampaignClose';
import RebatesCampaignText from './RebatesCampaignText'

import {REBATES_IMAGES_DOMAIN} from '../../../../../../utilities/src/rebates_const';

export default class RebatesCampaign extends Component{
    constructor(props) {
        super(props);
    }

    render() {
        const { forwardUrl, campaignInfo, campaignSummary, pointsBackBgImage } =
            this.props.toolbarCampaign;

        const div_style = {
            "width": "340px",
            "height": "120px",
            "background": "white",
            "backgroundSize": "100% 100%",
            "backgroundRepeat": "no-repeat",
            "fontFamily": "'ノーサンズ','Noto Sans', 'Noto Sans CJK JP', 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'メイリオ', 'Meiryo', 'Osaka', '游ゴシック', 'Yu Gothic', sans-serif",
            "fontSize": "12px",
        };

        if (pointsBackBgImage) {
            div_style.backgroundImage =
                "url(" + REBATES_IMAGES_DOMAIN + "/" + pointsBackBgImage + ")";
        }

        const wrapper = {
            "width": "88%",
            "margin": "15px auto",
        };

        const float_right = {
            "float": "right",
            "paddingRight": "7px",
            "paddingTop": "5px",
        };

        const clear = {
            "clear": "both",
            "paddingBottom": "10px",
        };

        return (
            <div style={div_style}>
                <div>
                    <div style={float_right}>
                        <RebatesCampaignClose onClose={this.props.onClose}/>
                    </div>
                </div>
                <div style={clear}/>
                <div style={wrapper}>
                    <RebatesCampaignText linkUrl={forwardUrl}
                                         campaignInfo={campaignInfo}
                                         campaignSummary={campaignSummary}
                                         onForward={this.props.onForward}/>
                </div>
            </div>
        );
    }
}

RebatesCampaign.propTypes = {
    toolbarCampaign: PropTypes.object,
    onForward: PropTypes.func,
    onClose: PropTypes.func,
};
